.list{
    margin:0 auto;
    height:1650px;
    /* width:350px; */
    /* border:1px solid red; */
    padding:0;
}
.list ul{
    padding:0;

}
.list ul li{
    border-radius:10px ;
    margin-left: 15px;
    /* border:1px solid blue; */
    list-style-type: none;
    display:block;
    float: left;
    width:163px;
    height:255px;
    background-color: white;
    margin-bottom: 15px;
    /* margin-right: 10px; */
}
.list ul li img{
    border-radius:10px ;
    width: 163px;
    height:163px

}
.list ul li span{
    margin-top: 10px;
    display: block;
    float: left;
}
