.part{
    padding:0 auto;
}

.im{
    position: relative;
    width:100%;
    height:375px;
    /* border:1px solid red; */
}
.im img{
    width:100%;
    height:375px;
}
.partone{
    padding-top:8px;
    font-size:16px;
    padding-left: 5px;
}
.parttwo{
    padding-top: 10px;
    color:orangered;
    margin-left:12px;
    font-size:24px;
}
.partthree{
    width:37px;
    height:37px;
    background-color: rgba(255,255,255, 0.2);
    position: absolute;
    left:22px;
    top:30px;
    border-radius: 37px;
}

.wei{
    width:100%;
    height:60px;
    background-color: white;
    border-top:2px solid rgb(241, 238, 238);
    position: fixed;
    bottom:0px;

}
.wei ul{
    padding:0;
    padding-left: 10px;
}
.wei ul li{
    display: block;
    float: left;  
    position: relative;
    /* top:5px;  */
    bottom: 5px;
}

.wei-t1{
    /* size:4rem 4rem; */
    margin-left: 10px;
}

.wei-t1 p{
    color:rgb(182, 180, 180);
    font-size:13px;
    position: relative;
    left:-5px;
}

.wei-b1{
    background-color: orange;
    width:110px;
    height:40px;
    border-radius: 20px 0px 0px 20px;
    /* border: 1px solid red; */
    color:white;
    font-size:17px;
    font-weight: bolder;
    text-align: center;
    line-height: 40px;
    position: relative;
    left:26px;
}

.wei-b2{
    background-color: orangered;
    width:110px;
    height:40px;
    border-radius:0px 20px 20px 0px;
    color:white;
    font-size:17px;
    font-weight: bolder;
    text-align: center;
    line-height: 40px;
    position: relative;
    left:26px;
}

